@import url(https://fonts.googleapis.com/css2?family=Manrope:wght@500;800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body * {
  font-family: 'Manrope', sans-serif;
}

div.nav-bar {
  background-color: #FDD835;
  color: #757575;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

div.nav-bar img {
  height: 20px;
  margin: 20px;
}

.nav-bar ul {
  display: flex;
}

.App-header > .nav-bar > nav > ul > li {
  display: block;
  text-decoration: none;
  list-style: none;
  font-weight: bold;
  margin-right: 15px;
  padding: 22px;
}

ul > li:last-child {
  margin-right: 0;
}

button {
  height: 40px;
  width: 200px;
  border-radius: 4px;
  border: solid 1px white;
  margin: 10px 15px;
}

button > span::before {
  content: "\f007";
}
* {
    margin: 0;
    padding: 0;
    font-family: 'Manrope', sans-serif;
    color: black;
}

.nav-bar > nav {
    width: 50%;
}

.nav-bar > nav > ul {
    display: flex;
    justify-content: space-around;
}

.nav-bar > nav > ul > li.active {
    background-color: #feeb9a;
}

/* Wrapper */
.wrapper {
    max-width: 1200px;
}

section.section {
    margin: 50px 0;
}

section.section > * {
    margin: 20px 70px;
}

.section-title {
    margin: 50px 0px 20px;
    border-bottom: 2px solid #000;
} 

.section-nav > ul {
    list-style: none;
    display: flex;
    justify-content: space-around;
    padding: 30px;
}

.section-nav > .nav-profile {
    padding: 0;
}

.section-nav li {
    color: #757575;
}

.section-nav li:hover, .section-nav li.active {
    color: #000;
    font-weight: bold;
}

main.section {
    width: 75%;
}
main.section > .section-nav {
    display: flex;
    justify-content: space-between;
}

main.container {
    margin: 0 70px;
    display: flex;
    justify-content: space-between;
}

.aside-ul {
    list-style-type: none;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.aside-li {
    color: #757575;
}

.aside button {
    margin: 0;
    font-size: 1em;
    background-color: #fff;
    color: #757575;
    text-align: left;
    border: none;
}

.graphic-header {
    margin-top: 25px;
    padding-bottom: 10px;
    border-bottom: 2px solid #000;
}

.aside-ul > .aside-li > .aside-li-text:focus {
    border:none;
}

.aside button:hover {
    color: #000000;
    border: none;
    font-weight: bold;
}

section ul.nav-profile {
    list-style-type: none;
    display: flex;
    justify-content: space-around;
}

.nav-profile-item {
    padding: 8px 20px 0 0;
    font-size: 16px;
    font-weight: 400;
    letter-spacing: 0.5px;
}

.nav-profile-item:hover {
    font-weight: 700;
}

.title {
    margin-top: 50px;
    margin-bottom: 40px;
}
.container-title {
    font-size: 36px;
    font-weight: 700;
    text-align: center;
    padding-bottom: 20px;
}
.section-info {
    font-size: 24px;
    font-weight: 400;
    text-align: center;
}

/* container-button */
.container-button {
    display: flex;
    justify-content: center;
}
.container-lang {
    display: flex;
    flex-direction: column;
    margin-right: 50px;
}
.btn-text {
    font-size: 18px;
    font-weight: 400;
    padding-bottom: 20px;
}
.btn {
    font-size: 18px;
    font-weight: 400;
    background: transparent;
    color: black;
    padding: 15px 70px;
    margin-bottom: 3px;
}
.btn:hover {
    background: #CAAC2A;
}

