/* index_t */
.container-test {
    display: flex;
    margin-bottom: 20px;
}

/* question */
.test-question {
    display: flex;
    width: 40vw;
    flex-direction: column;
    margin-right: 50px;
}
.question-title {
    font-size: 18px;
    font-weight: 400;
    padding-bottom: 20px;
}
.question-text {
    width: 100%;
    height: 60%;
    font-size: 18px;
    font-weight: 400;
    border: 2px solid gray;
}

/* answer */
.test-answer {
    display: flex;
    flex-direction: column;
}
.answer-title {
    font-size: 18px;
    font-weight: 400;
    padding-bottom: 20px;
}
span.answer {
    padding-left: 10px;
    font-size: 18px;
    font-weight: 400; 
}

/* footer */
.footer-text {
    display: inline;
    font-size: 18px;
    font-weight: 400;
    margin-left: 110px;
    margin-right: 20px;
}
.time {
    font-size: 18px;
    font-weight: 400;
    margin-right: 50px;
}
.btn-prev {
    width: auto;
    font-size: 18px;
    font-weight: 400;
    margin-top: 14px;
    margin-right: 50px;
    padding: 7px 40px;
    background: #FFEFAF;
}
.btn-next {
    width: auto;
    font-size: 18px;
    font-weight: 400;
    margin-top: 14px;
    padding: 7px 40px;
    background: #FFEFAF;
}

footer {
    display: flex;
    align-items: center;
}

.footer-partition-right {
    display: flex;
    width: 50vw;
}

.footer-partition-left {
    margin-right: 50px;
    width: 40vw;
}


